import React from 'react';
import styled from 'styled-components';

function Button (props) {
  const tam = `16vh`;
  const alt = `6vh`;

  return (
    <ButtonWrapped>

      <div className="svg-wrapper">

        <a href={props.tag}>

          <svg height={alt} width={tam} xmlns="http://www.w3.org/2000/svg">
            <rect className="shape" height={alt} width={tam} rx="100" />
          </svg>
          <div className="text">{props.nome}</div>

        </a>

      </div>

    </ButtonWrapped>
  );
}

export default Button;

const ButtonWrapped = styled.div`

a{
  text-decoration: none;
}

.svg-wrapper {
  height: 6vh;
  position: relative;
  width: 17.5vh;

}

.shape {
  fill: transparent;
  stroke-dasharray: 0vh 1vh;
  stroke: yellow;
  
}

.text {
  color: black;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  text-align: center;
  font-size: 1.8vh;
  position: relative;
  top: -4.5vh;
}

@keyframes draw {
  0% {
    stroke-dasharray: 140 540;
    stroke-dashoffset: -474;
    stroke-width: 2px;
  }
  100% {
    stroke-dasharray: 760;
    stroke-dashoffset: 0;
    stroke-width: 4px;
  }
}

.svg-wrapper:hover .shape {
  -webkit-animation: 0.5s draw linear forwards;
  animation: 0.5s draw linear forwards;
}
`;
