import React from 'react';
import styled from 'styled-components';
import mainBcg from '../../images/site.png';
import IconDown from './IconDown';
import Typical from 'react-typical';
/* import Galaxy from '../../pages/Galaxy'; */

export default function Projects({ img = mainBcg }) {
  return (
    <>
      <ProjectsWrapper img={img}>

        <main id="home">
          {/* <Galaxy /> */}
          <main-body className="container-fluid">


            <h1 className="lg-heading">
              Everton <span className="text-secondary">Sávio</span>
            </h1>
            <h1 className="sm-heading">
              <Typical
                steps={[
                  '#',
                  2000,
                  '#Sofware',
                  500,
                  '#Software ',
                  1000,
                  '#Software Engineer',
                  1500,
                  '#Software Engineer <>',
                  500,
                  '#Software Engineer <Java, React>'
                ]}
                loop={1}
                wrapper="p"
              />
              {/* Data Scientist Full-Stack */}
            </h1>
            <div className="icone">
              <IconDown />
            </div>
          </main-body>
        </main>
      </ProjectsWrapper>
    </>
  );
}
/* 
  background: linear-gradient(var(--primaryRGBA), var(--primaryRGBA)),
    url(${props => props.img}) center/ cover no-repeat;
 */
const ProjectsWrapper = styled.div`
background: linear-gradient(var(--primaryRGBA), var(--primaryRGBA)),
    url(${props => props.img}) center/ cover no-repeat;
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.5;
  border-radius: 5px;
  border-radius: 5px;

  .icone {
    text-align:center;
  }
  h1,
  h2,
  h3 {
    margin: 0;
    font-weight: 400;
    color: #fff;
    &.lg-heading {
      position: absolute;
      top: 20vh;
      margin-left: 1vw;
      margin-right: 1vw;
      font-size: 7vh;
      padding-bottom: 1rem;
    }

    &.sm-heading {
      margin-left: 1vw;
      margin-right: 1vw;
      position: absolute;
      top: 30vh;
      width: 98%;
      padding: 1rem 0rem 0.5rem 0.5rem;
      font-size: 4vh;
      border-radius: 5px;
      background-color: #282c34;
      letter-spacing: 0.2ch;
      font-weight: 400;
      opacity: 0.7;
      border: 1px solid whitesmoke;
      
    }
  }

  .text-secondary {
    color: #eece1a !important;
  }

  main {
    height: 100vh;
    padding-bottom: 30vh;
  }
`;
