import React from 'react';
import styled from 'styled-components';
import * as ReactSpring from 'react-spring';
import * as ReactUseGesture from 'react-use-gesture';
import classNames from 'classnames';
import pages from './DataPages';
const R = require('ramda');

function _extends() {
  _extends =
    Object.assign ||
    function (target) {
      for (var i = 1; i < arguments.length; i++) {
        var source = arguments[i];
        for (var key in source) {
          if (Object.prototype.hasOwnProperty.call(source, key)) {
            target[key] = source[key];
          }
        }
      }
      return target;
    };
  return _extends.apply(this, arguments);
}
const {
  useSprings,
  interpolate,
  useTransition,
  animated,
  useSpring,
} = ReactSpring;
const { useGesture } = ReactUseGesture;
const cx = classNames;

const Button = ({ children, className, ...rest }) => {
  const classNames = cx('button', className);

  return React.createElement(
    'button',
    _extends({ className: classNames }, rest),
    children
  );
};

const Container = React.forwardRef(
  ({ className, as: Component, children, ...props }, ref) => {
    const classNames = cx('container', className);

    return React.createElement(
      Component,
      _extends({ className: classNames, ref: ref }, props),
      children
    );
  }
);

Container.defaultProps = {
  as: 'div',
};

const Address = ({ data }) =>
  React.createElement(
    'div',
    null,
    React.createElement(
      'address',
      { className: 'header__address' },
      React.createElement('i', { className: 'fas fa-map-marker-alt' }),
      data.place
    )
  );

const HeaderTitle = ({ data }) =>
  React.createElement('h1', { className: 'header__title' }, data.title);

const HeaderDate = ({ data }) =>
  React.createElement('time', { className: 'header__date' }, data.date);

const HeaderTop = ({ data }) =>
  React.createElement(
    'div',
    { className: 'header__top' },
    React.createElement(HeaderTitle, { data: data }),
    React.createElement(HeaderDate, { data: data })
  );

const HeaderItem = ({ data }) => {
  return React.createElement(
    Container,
    { className: 'header__item' },
    React.createElement(HeaderTop, { data: data }),
    React.createElement(Address, { data: data })
  );
};

const Header = ({ data, index, as: Component }) => {
  const [prev] = React.useState({ index });

  const transitions = useTransition(index, (p) => p, {
    from: (newIdx) => {
      const dir = newIdx - prev.index;
      prev.index = newIdx;
      return { opacity: 0, transform: `translate3d(0, ${100 * dir}%, 0)` };
    },
    enter: () => {
      return { opacity: 1, transform: `translate3d(0, 0%, 0)` };
    },
    leave: (newIdx) => {
      const dir = newIdx - index;
      return { opacity: 0, transform: `translate3d(0, ${100 * dir}%, 0)` };
    },
  });

  return React.createElement(
    'div',
    { className: 'header' },
    transitions.map(({ item, props, key }) => {
      return React.createElement(
        animated.div,
        {
          key: key,
          style: props,
        },
        React.createElement(Component, { data: data[item] })
      );
    })
  );
};

/* const Footer = () => {
  return React.createElement (
    Container,
    {className: 'footer'},
    React.createElement (
      Button,
      {className: 'burger-button'},
      React.createElement ('i', {className: 'fas fa-bars'})
    ),
    React.createElement (
      Button,
      {className: 'event-button'},
      React.createElement ('i', {className: 'fas fa-plus'}),
      'Create Event'
    )
  );
}; */

const MAX_COUNT = 3;

const onNext = (
  { args: [index], down },
  { isNextTrigger, getStartData, onChange, ref, drag },
  i
) => {
  if (!isNextTrigger || down) {
    return;
  }

  if (index === i) {
    return {
      ...getStartData(i),
      x: -ref.current.offsetWidth,
      sc: 1,
      opacity: 1,
      immediate: false,
      onStart: () => {
        onChange(index + 1);
      },
      onRest: () => {
        drag.cancel();
      },
    };
  }

  if (i - index > 0) {
    const currentIndex = i - index - 1;
    return {
      ...getStartData(currentIndex),
      immediate: false,
      onStart: undefined,
      onRest: undefined,
    };
  }
};

const onPrev = (
  { args: [index], down },
  { isPrevTrigger, getStartData, onChange, drag },
  i
) => {
  if (!isPrevTrigger || down) {
    return;
  }

  if (index > 0 && i - index >= -1 && isPrevTrigger) {
    const currentIndex = i - index + 1;

    let indexConf = {
      onStart: undefined,
      onRest: undefined,
    };

    if (index === i) {
      indexConf.onStart = () => {
        onChange(index - 1);
      };

      indexConf.onRest = () => {
        drag.cancel();
      };
    }

    return {
      ...getStartData(currentIndex),
      immediate: false,
      ...indexConf,
    };
  }
};

const isNotTarget = ({ args: [index] }, data, i) => {
  if (index !== i) {
    return {
      onStart: undefined,
      onRest: undefined,
    };
  }
};

const isNotDown = ({ down }, { drag }) => {
  if (down) {
    return;
  }

  return {
    x: 0,
    immediate: false,
    onRest: () => {
      drag.cancel();
    },
  };
};

const onDefault = ({ movement }) => ({
  x: movement[0],
  immediate: true,
  reset: false,
  onStart: undefined,
  onRest: undefined,
});

const useIsDrag = () => {
  const [state] = React.useState({});
  const setIsDrag = (movement) => {
    const xMod = Math.abs(movement[0]);

    if (xMod > 2) {
      state.isDrag = true;
    }
  };

  const cancel = () => {
    state.isDrag = false;
  };

  return {
    state,
    setIsDrag,
    cancel,
  };
};

const useSlider = ({ data, onChange, ref, onClick }) => {
  const totalItems = data.length;
  const drag = useIsDrag();
  const getStartData = (i) => {
    const isVisible = i <= MAX_COUNT - 1;
    const multiplier = i === 0 ? 1 : 1.1;

    return {
      x: isVisible ? (i * 28) / multiplier : (MAX_COUNT - 1) * 28,
      y: 0,
      sc: isVisible
        ? 1 / (MAX_COUNT / (MAX_COUNT * multiplier - i))
        : 1 / MAX_COUNT,
      opacity: isVisible ? 1 / (MAX_COUNT / (MAX_COUNT - i)) : 0,
      display: 'block',
      pointerEvents: i === 0 ? '' : 'none',
      boxShadow:
        i === 0
          ? '0 10px 40px rgba(0, 0, 0, .2)'
          : '0 0px 0px rgba(0, 0, 0, .2)',
      position: '',
      transformOrigin: '',
      width: '',
      height: '',
    };
  };
  const [props, set] = useSprings(totalItems, getStartData);

  const bind = useGesture({
    onDrag: (state) => {
      const {
        args: [index],
        down,
        movement,
        cancel,
      } = state;
      const isNextTrigger = movement[0] <= -100 && index !== totalItems - 1;
      const isPrevTrigger = movement[0] >= 24;

      const data = {
        isNextTrigger,
        isPrevTrigger,
        getStartData,
        onChange,
        ref,
        drag,
      };

      if (down) {
        drag.setIsDrag(movement);
      }

      if ((isNextTrigger || isPrevTrigger) && down) {
        cancel(index);
      }

      set((i) => {
        const action = R.find((fn) => fn(state, data, i), [
          onNext,
          onPrev,
          isNotTarget,
          isNotDown,
          onDefault,
        ]);

        if (action) {
          return action(state, data, i);
        }
      });
    },
  });

  const map = (component) => {
    return props.map(
      ({ x, y, display, sc, opacity, pointerEvents, boxShadow, ...rest }, i) => {
        const root = {
          key: i,
          style: {
            display,
            transform: interpolate(
              [x, y, sc],
              (x, y, s) => `translate3d(${x}px, ${y}px, 0) scale(${s})`
            ),
            opacity,
            pointerEvents,
            zIndex: data.length - i,
            ...rest,
          },

          ...bind(i),
        };

        const inner = {
          style: {
            boxShadow,
          },
        };

        const onClickHandler = (...args) => {
          if (drag.state.isDrag) {
            return;
          }

          onClick(set, i, ...args);
        };

        return component({
          root,
          inner,
          ref,
          item: data[i],
          onClick: onClickHandler,
        });
      }
    );
  };

  return {
    map,
    ref,
    set,
    getStartData,
  };
};

const Slider = ({ slider }) => {
  const { map, ref } = slider;

  return React.createElement(
    'div',
    { className: 'slider', ref: ref },
    React.createElement(
      'div',
      { className: 'slider__container' },
      map(({ root, inner, onClick, item, itemRef }) => {
        const videoSrc = R.path(['video'])(item);
        const videoPoster = R.path(['poster'])(item);

        return React.createElement(
          animated.div,
          _extends(
            { ref: itemRef, className: 'slider__item', onClick: onClick },
            root
          ),
          React.createElement(
            animated.div,
            _extends(
              {
                className: 'slider__inner',
              },
              inner,
              {
                style: {
                  backgroundImage: `url(${R.path(['image'])(item)})`,
                  ...inner,
                },
              }
            ),
            videoSrc &&
            React.createElement('video', {
              src: videoSrc,
              poster: videoPoster,
              autoPlay: true,
              loop: true,
              muted: true,
              playsInline: true,
            })
          )
        );
      })
    )
  );
};

const ScreenModal = ({ onClose, isOpen, children }) => {
  const mainSpring = useSpring({
    from: { x: 0 },
    x: isOpen ? 1 : 0,
    pointerEvents: isOpen ? '' : 'none',
  });

  const contentSpring = useSpring({
    from: { x: 0 },
    x: isOpen ? 1 : 0,
    pointerEvents: isOpen ? '' : 'none',
    config: { duration: 400 },
  });

  return React.createElement(
    ScreenModalRoot,
    { mainSpring: mainSpring },
    React.createElement(
      ScreenModalTopBar,
      { mainSpring: mainSpring },
      React.createElement(
        Button,
        { className: 'nav-bar__back', onClick: onClose },
        React.createElement('i', { className: 'fas fa-long-arrow-alt-right' }),
        'VOLTAR'
      )
    ),
    React.createElement(ScreenModalContainer, {
      mainSpring: mainSpring,
      contentSpring: contentSpring,
      children: children,
    })
  );
};

const ScreenModalRoot = ({ mainSpring, children }) =>
  React.createElement(animated.div, {
    className: 'screen-modal-root',
    style: {
      pointerEvents: mainSpring.pointerEvents,
    },

    children: children,
  });

const ScreenModalTopBar = ({ mainSpring, children }) =>
  React.createElement(Container, {
    as: animated.div,
    style: {
      color: 'white',
      opacity: mainSpring.x.interpolate({ range: [0, 1], output: [0, 1] }),
      transform: mainSpring.x
        .interpolate({
          range: [0, 1],
          output: [100, 0],
        })
        .interpolate((x) => `translate3d(0, ${x}%, 0`),
    },

    children: children,
  });

const ScreenModalContainer = ({ mainSpring, contentSpring, children }) =>
  React.createElement(
    animated.div,
    {
      className: 'screen-modal',
      style: {
        transform: mainSpring.x
          .interpolate({
            range: [0, 1],
            output: [100, 0],
          })
          .interpolate((x) => `translate3d(0, ${x}%, 0`),
      },
    },
    React.createElement(
      animated.div,
      {
        className: 'screen-modal__scroll',
        style: {
          opacity: contentSpring.x.interpolate({
            range: [0, 1],
            output: [0, 1],
          }),
          transform: contentSpring.x
            .interpolate({
              range: [0, 1],
              output: [100, 0],
            })
            .interpolate((x) => `translate3d(0, ${x}%, 0`),
        },
      },
      children
    )
  );

const ScreenModalContent = ({ data }) =>
  React.createElement(
    'div',
    null,
    React.createElement(
      Container,
      { className: 'screen-modal__content' },
      React.createElement(HeaderTitle, { data: data }),
      React.createElement(Address, { data: data }),
      React.createElement(HeaderDate, { data: data })
    ),
    React.createElement(
      Container,
      null,
      React.createElement('p', null, data.text)
    )
  );

const Screen = () => {
  const [current, setCurrent] = React.useState(0);
  const [isOpened, setIsOpened] = React.useState(false);
  const ref = React.useRef();
  const slider = useSlider({
    data: pages,
    onChange: setCurrent,
    ref,
    onClick: (set, index, event) => {
      const el = event.currentTarget;
      const rect = el.getBoundingClientRect();

      const ws = window.innerWidth / el.offsetWidth;
      const hs = window.innerHeight / el.offsetHeight;

      set((i) => {
        if (index === i) {
          setIsOpened(true);

          return {
            x: window.innerWidth / 2 - rect.width / 2 - rect.x,
            y: window.innerHeight / 2 - rect.height / 2 - rect.y,
            sc: Math.max(ws, hs),
            pointerEvents: 'none',
            transformOrigin: 'center center',
          };
        }
      });
    },
  });

  const onCloseClick = () => {
    slider.set((i) => {
      if (i === current) {
        setIsOpened(false);
        return {
          ...slider.getStartData(0),
          transformOrigin: 'center center',
        };
      }
    });
  };

  const mainSpring = useSpring({
    from: { x: 0 },
    x: !isOpened ? 1 : 0,
  });

  const data = pages[current];

  return React.createElement(
    'section',
    { className: 'events-screen' },
    React.createElement(
      Container,
      {
        as: animated.div,
        className: 'nav-bar',
        style: {
          opacity: mainSpring.x.interpolate({ range: [0, 1], output: [0, 1] }),
          transform: mainSpring.x
            .interpolate({
              range: [0, 1],
              output: [-100, 0],
            })
            .interpolate((x) => `translate3d(0, ${x}%, 0`),
        },
      }
      /*       React.createElement(
        Button,
        {className: 'nav-bar__back'},
        React.createElement('i', {className: 'fas fa-long-arrow-alt-right'}),
        'VOLTAR'
      ) */
    ),
    React.createElement(Header, { data: pages, index: current, as: HeaderItem }),
    React.createElement(
      'div',
      { className: 'events-screen__slider' },
      React.createElement(Slider, { slider: slider })
    ),
    React.createElement(
      ScreenModal,
      { onClose: onCloseClick, isOpen: isOpened },
      React.createElement(ScreenModalContent, { key: data.id, data: data })
    )
    /* React.createElement (Footer, null) */
    /* React.createElement (
      Container,
      null,
      React.createElement (
        'div',
        {className: 'credits'},
        'Created with ',
        React.createElement ('span', {class: 'love'}),
        ' by ',
        React.createElement (
          'a',
          {href: 'https://twitter.com/odintsov_design', target: '_blank'},
          'Ivan Odintsov'
        ),
        '\xA0\xA0\xB7\xA0\xA0Inspired by ',
        React.createElement (
          'a',
          {
            href: 'https://dribbble.com/shots/3731362-Event-cards-iOS-interaction',
            target: '_blank',
          },
          'Divan Raj'
        )
      )
    ) */
  );
};

const AppLayout = ({ children }) => {
  return React.createElement('div', { className: 'app-layout' }, children);
};

const ProjectsSlider = () => {
  return (
    <ProjectsSliderWrapper>
      <div className="main" id="portfolio">
        {React.createElement(
          AppLayout,
          null,
          React.createElement(Screen, null)
        )}
      </div>
    </ProjectsSliderWrapper>
  );
};

export default ProjectsSlider;

const ProjectsSliderWrapper = styled.div`
  button {
    border: 0;
    border-radius: 0;
    background: transparent;
    color: inherit;
    font-size: 1em;
    line-height: 1.2;
    white-space: nowrap;
    text-decoration: none;
    font-family: avenir next, avenir, helvetica neue, helvetica, ubuntu, roboto,
      noto, segoe ui, arial, sans-serif;
    padding: 0;
    cursor: pointer;
  }

  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }

  .app-layout {
    background-color: #fff;
    box-shadow: 0 0 60px rgba(0, 0, 0, 0.2);
    width: 100%;
    border-radius: 5px;
    height: 100%;
    max-width: 100%;
    padding-right: 0vw;
    margin-top: auto;
    position: absolute;
    overflow: hidden;
    /* margin: auto; */
    display: flex;
    flex-direction: column;
    padding-top: calc(var(--container-padding) / 1.5);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    &__system-bar {
      position: relative;
      z-index: -1;

      img {
        width: 100%;
      }
    }
  }

  .container {
    padding: 0 var(--container-padding);
  }

  .slider {
    height: 100%;
  }

  .slider__container {
    position: relative;
    height: 100%;
  }

  .slider__item {
    position: absolute;
    top: 0;
    left: 0;
    border: 1.2px solid black;
    border-radius: 5px;
    width: 100%;
    height: 100%;
    transform-origin: right center;
    will-change: transform, opacity;
  }

  .slider__inner {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    width: 100%;
    height: 100%;
    will-change: box-shadow;
    border-radius: 5px;
    overflow: hidden;
    transform: translate3d(0, 0, 0);

    > video {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }

  .screen-modal__scroll {
    height: 100%;
    will-change: transform, opacity;

    > div {
      height: 100%;
      overflow: auto;
    }
  }

  .screen-modal-root {
    --container-padding2: calc(var(--container-padding) / 1.5);
    --container-padding3: calc(var(--container-padding) / 2);
    --margin: calc(var(--container-padding) / 4);

    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 6;

    > .container {
      will-change: transform, opacity;
    }

    .container {
      padding: 0 var(--container-padding2);
    }
  }

  .screen-modal {
    background-color: whitesmoke;
    position: absolute;
    height: 50%;
    width: 100%;
    left: 0;
    bottom: 0;
    will-change: transform;

    .container {
      padding: var(--container-padding3) var(--container-padding2);
    }

    .screen-modal__content {
      padding-top: var(--container-padding);
      border-bottom: 1px solid #d8d8d8;

      .header__title {
        margin-bottom: var(--margin);
        padding-right: 0;
      }

      .header__address {
        margin-bottom: var(--margin);
      }
    }

    p {
      margin-top: 0;
    }
  }

  .header {
    position: relative;
    height: 16vh;
    overflow: hidden;
    margin: 20px 0;
    will-change: transform, opacity;

    > div {
      position: absolute;
      will-change: transform, opacity;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
    }
  }

  .header__item {
    width: 100%;
  }

  .header__title {
    font-size: 4vh;
    font-weight: 700;
    margin-top: 0;
    margin-bottom: 0;
    line-height: 1;
    flex: 1;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-right: 2vh;
  }

  .header__top {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 10px;
  }

  .header__address {
    font-size: 15px;
    font-weight: 600;
    display: flex;
    align-items: baseline;
    font-style: normal;

    .fas {
      margin-right: 10px;
    }
  }

  .header__date {
    font-size: 15px;
    font-weight: 600;
  }

  /*   .footer {
    display: flex;
    justify-content: space-between;
    padding-bottom: 20px;
  } */

  .button {
    padding: calc(var(--container-padding) / 3);
  }

  .events-screen {
    height: 90%;
    position: relative;
    flex: 1;
    display: flex;
    flex-direction: column;
  }

  .events-screen__slider {
    position: relative;
    height: calc(70% - 75px);
    margin-bottom: 25px;
    margin-left: 0px;
    width: 70%;
    flex: 1;

    .slider {
      padding-left: var(--container-padding);
    }
  }

  .burger-button {
    font-size: 25px;
  }

  .event-button {
    font-size: 2vh
    font-weight: 700;
    display: flex;
    align-items: center;
    line-height: 1;

    .fas {
      font-size: 12px;
      margin-right: 5px;
    }
  }

  .nav-bar {
  }

  .nav-bar__back {
    position: absolute;
    background-color: black;
    margin-top: 4vh;
    margin-left: 6vh;
    padding: 5vh;
    font-size: 2vh;
    font-weight: 700;
    line-height: 1;
    display: flex;
    border-radius: 5px;
    border: 1px solid palevioletred;
    align-items: baseline;
  }

  .credits {
    font-size: 0.8em;
    margin-bottom: 1em;
    text-align: center;

    a {
      font-weight: 600;
      color: #91a7ff;
      text-decoration: none;

      &:hover {
        color: #5c7cfa;
        text-decoration: underline;
      }
    }
  }

  .love {
    background: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/42764/heart-smil.svg);
    display: inline-block;
    height: 16px;
    vertical-align: middle;
    width: 16px;
  }
`;
