import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import SmoothScroll from 'smooth-scroll';
import SectionHome from '../components/HomePageComponents/SectionHome';
import SectionProjects from '../components/HomePageComponents/SectionProjects';
import SectionAboutMe
  from '../components/HomePageComponents/SectionAboutMeClassic';
import SectionContact from '../components/HomePageComponents/SectionContact';

//yarn add three react-three-fiber
//yarn lerp
//yarn add smooth-scroll
//yarn add threejs-meshline

const HomePage = () => {
  const scroll = new SmoothScroll('a[href*="#"]', {
    speed: 1000,
  });
  console.log(scroll);

  const [size, setSize] = useState(
    () =>
      window.innerWidth <= 1200 || window.innerHeight <= 650 ? '0.5vw' : '8vw'
  );

  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth <= 1200 || window.innerHeight <= 650) {
        setSize('0.5vw');
      } else {
        setSize('8vw');
      }
    };
    window.addEventListener('resize', handleResize);
    /////BOA PRATICA, LIMPAR EVENTO COM RETURN//
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  });

  return (
    <HomeWrapper tamanho={size}>
      <div className=" wrapper">
        <div className="ses1">
          <SectionHome />
        </div>
        <div className="ses2">
          <SectionAboutMe />
        </div>
        <div className="ses3">
          <SectionProjects />
        </div>
        <div className="ses4">
          <SectionContact />
        </div>
      </div>
    </HomeWrapper>
  );
};

const HomeWrapper = styled.div`

  .ses1{
    position: relative;
    z-index: 0;
  }
  .ses2{
    position: relative;
    z-index: 1;
  }

    background-color: #282c34 /*#141415 #020206 //#282c34 */;

  .wrapper {
    padding-left: ${props => props.tamanho}};
    padding-right: 0.5vh;
    overflow-x: hidden;
  }
`;

export default HomePage;
