import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import ProjectSlider from './ProjectsComponents/ProjectsSlider';
import note from '../../images/images.jpg';

const SectionProjects = () => {
  const [size, setSize] = useState([window.innerWidth, window.innerHeight]);
  const [width, height] = [...size];

  useEffect(() => {
    const handleResize = () => {
      setSize([window.innerWidth, window.innerHeight]);
    };
    window.addEventListener('resize', handleResize);
    /////BOA PRATICA, LIMPAR EVENTO COM RETURN//
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  });

  if (width < 1200) {
    return (
      <SectionProjectsWrapper note={note}>
        <div className="main" id="portfolio">
          <ProjectSlider />
        </div>
      </SectionProjectsWrapper>
    );
  } else {
    return (
      <SectionProjectsWrapper note={note}>
        <div className="main" id="portfolio">
          <ProjectSlider />
          <div className="info">
            <h5> Instruções:</h5>
            <p>
              1. Utilizar Google Chrome.
            </p>
            <p>
              2. Clique no card ao lado para mais informações sobre o projeto.
            </p>
            <p>
              3. Deslize para direita ou esquerda para vizualizar outro projeto.
            </p>
          </div>
        </div>
      </SectionProjectsWrapper>
    );
  }
};

export default SectionProjects;

const SectionProjectsWrapper = styled.div`
  h5 {
    margin-top: 3vh;
    font-size: 2vh;
  }
  p {
    font-size: 1.7vh;
    margin: 0;
    padding-bottom: 1vh;
  }
  .main {
    position: relative;
    margin-top: 0.5vh;
    border-radius: 5px;
    background-color: rgba(255, 255, 255, 1);
  }
  #portfolio {
    border-radius: 5px;
    min-height: 100vh;
    margin-top: 0.5vh;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 112.5%;
    line-height: 1.6em;
  }
  .info {
    padding: 0.8vh;
    width: 30vh;
    height: 45vh;
    background: url(${(props) => props.note}) center/ cover no-repeat;
    border: 0.5px solid black;
    border-radius: 5px;
    margin-top: 35vh;
    margin-right: 5vh;
    transition: 2s;
    position: absolute;
    right: 0;
    top: 0;
    box-shadow: 2px 2px 1px 0px rgba(0, 0, 0, 0.75);
    &:hover {
      -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
      -moz-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
      box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
    }
  }
`;
